<template>
    <div id="charts">
        <el-radio-group v-model="season" @change="seasonChange">
            <el-radio :label="0">供暖季</el-radio>
            <el-radio :label="1">供冷季</el-radio>
        </el-radio-group>

        <el-button size="medium" v-show="isEvaluateItem" type="text" @click="showResult=!showResult"
                   class="show-result">
            评估结果<i :class="showResult?'el-icon-arrow-down':'el-icon-arrow-right'"></i>

        </el-button>

        <el-card class="result-card" v-if="showResult">
            <div v-if="hasMark">

                <!--<div>将各项指标按照从优到劣进行排序并划分成[0,25%)（最优），[25%-50%)，[50%-75%)，[75%-100%]（最劣）四个区间，</div>-->
                <div style="margin-bottom: 10px">您所评估的项目，系统能效比EER在所有项目中排{{evaluateStr.eer.str}};</div>
                <div>其他各项分指标排序依次处于以下区间：</div>
                <div style="margin-left: 20px"> 地源侧温度指标为{{evaluateStr.tgo.str}}，水泵能耗指标为{{evaluateStr.tg.str}}，

                </div>
                <div style="margin-left: 20px">
                    主机能耗比指标为{{evaluateStr.a.str}}，完善度指标为{{evaluateStr.n.str}}，主机COP为{{evaluateStr.cop.str}};
                </div>
                <div style="margin-left: 20px;margin-bottom: 10px;">
                    辅助指标ΔEER与EER的和为{{evaluateStr.sumEER.value}}，在所有项目中排{{evaluateStr.sumEER.str}}。
                </div>
                <div v-if="evaluateStr.tg.isBad || evaluateStr.tgo.isBad || evaluateStr.a.isBad||evaluateStr.n.isBad">
                    经评估，该项目
                    <span v-if="evaluateStr.tgo.isBad">地源侧温度指标</span>
                    <span v-if="evaluateStr.tg.isBad"><span v-if="evaluateStr.tgo.isBad">、</span>水泵能耗指标</span>
                    <span v-if="evaluateStr.a.isBad"><span v-if="evaluateStr.tg.isBad||evaluateStr.tgo.isBad">、</span>主机能耗比</span>
                    <span v-if="evaluateStr.n.isBad"><span
                            v-if="evaluateStr.tg.isBad || evaluateStr.tgo.isBad || evaluateStr.a.isBad">和</span>完善度指标</span>
                    <span v-if="evaluateStr.tg.isBad || evaluateStr.tgo.isBad || evaluateStr.a.isBad|| evaluateStr.n.isBad">明显低于基线水平,需要</span>

                    <span v-if="evaluateStr.tgo.isBad">优化地源侧的运行</span>
                    <span v-if="evaluateStr.tg.isBad"><span v-if="evaluateStr.tgo.isBad">、</span>优化地源侧的输配情况</span>
                    <span v-if="evaluateStr.a.isBad"><span v-if="evaluateStr.tg.isBad||evaluateStr.tgo.isBad">、</span>降低水泵的运行能耗占比</span>
                    <span v-if="evaluateStr.n.isBad"><span
                            v-if="evaluateStr.tg.isBad || evaluateStr.tgo.isBad || evaluateStr.a.isBad">和</span>改善主机的运行情况</span>
                </div>
                <div v-else>经评估，该项目地源侧温度指标、水泵能耗指标、主机能耗比、完善度指标优异。</div>
                <div style="margin-top: 10px;font-size: 12px;">
                    <div>注：各项指标按照从优到劣进行排序并划分区间，依次为:</div>
                    <div style="margin-left: 25px">[0,25%)（最优），[25%-50%)，   [50%-75%)，[75%-100%]（最劣）。</div>
                </div>

            </div>
            <div v-else>您没有上传{{season === 0 ? '供暖季' : '供冷季'}}数据，无法评估。</div>

        </el-card>
        <!--  <el-collapse v-model="activeName" accordion>
              <el-collapse-item title="评估结果" name="0">
              </el-collapse-item>
          </el-collapse>-->

        <el-tabs v-model="activeTab" type="card" @tab-click="tabClick">
            <el-tab-pane label="各参数单独排序" name="singleSort">
                <!--:markEigen="markEigen" -->
                <!--:markDeltaEER="markDeltaEER"-->

                <params-list ref="singleSortBar" :activeTab="'singleSort'" :isEvaluateItem="isEvaluateItem"
                             :season="season"
                             @setEvaluateStr="setEvaluateStr" @eersort="eerSortClick" @getMark="getMark"
                             v-show="projectList.length!=0"></params-list>
                <eerparam ref="singleSortScatter" :activeTab="'singleSort'" :isEvaluateItem="isEvaluateItem"
                          @setSumEEREvaluate="setSumEEREvaluate" :isEerDesc="isSEerDesc"
                          v-show="projectList.length!=0"></eerparam>
                <div class="none-item" v-if="projectList.length==0">暂无数据，如果已有项目，请注意区分冷暖季数据</div>
            </el-tab-pane>

            <el-tab-pane label="按EER排序" name="eerSort">
                <params-list ref="eerSortBar" :activeTab="'eerSort'" :isEvaluateItem="isEvaluateItem" :season="season"
                             @eersort="eerSortClick" v-show="projectList.length!=0"></params-list>
                <eerparam ref="eerSortScatter" :activeTab="'eerSort'" :isEvaluateItem="isEvaluateItem"
                          :isEerDesc="isEEerDesc" v-show="projectList.length!=0"></eerparam>
                <div class="none-item" v-if="projectList.length==0">暂无数据，如果已有项目，请注意区分冷暖季数据</div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
    import paramsList from '../Project/ParamsList.vue'
    import eerparam from '../Project/EERparam.vue'
    import calculate from '../../../common/calculate.js'

    export default {
        data() {
            return {
                season: 0,//供暖
                activeTab: 'singleSort',
                projectList: [],
                allEigenList: [],//所有项目的特征值
                allDeltaEERList: [],
                isSEerDesc: true,
                isEEerDesc: true,
                isEvaluateItem: false,
                activeName: '0',
                evaluateStr: {
                    tgo: {isBad: false, str: ''},
                    tg: {isBad: false, str: ''},
                    a: {isBad: false, str: ''},
                    n: {isBad: false, str: ''},
                    cop: {isBad: false, str: ''},
                    eer: {isBad: false, str: ''},
                    sumEER: {value: '', isBad: false, str: ''},
                },

                showResult: false,
                hasMark: false
            }
        },
        components: {
            paramsList,
            eerparam
        },
        watch: {
            '$route'(to, from) {
                if (this.$route.query && this.$route.query.id) {
                    this.isEvaluateItem = true;
                }
                this.getData();
            }
        },
        created() {
            if (this.$route.query && this.$route.query.id) {
                this.isEvaluateItem = true;
            }
            this.getData();
        },
        methods: {
            getData(){
                var self = this;
                let url = GSHP_SERVER_ENDPOINT + '/project/projectAveParams';
                self.$http.get(url, {
                    params: {
                        season: self.season,
                    }
                }).then((response) => {
                    if (response.status === 200) {
                        self.projectList = response.data;
                        this.calculateValue();
                    }
                })
            },
            /*计算特征值和Delta EER*/
            calculateValue(){
                this.allEigenList = calculate.getTableData(this.projectList, this.season);//所有项目的特征值
                this.allDeltaEERList = calculate.getDeltaEER(this.allEigenList, this.season);
                var obj = {
                    allEigenList: this.allEigenList,
                    allDeltaEERList: this.allDeltaEERList
                };
                this.$store.dispatch('initEvaluateState', obj);
            },

            seasonChange(val){
                this.showResult = false;
                this.getData();
            },

            tabClick(val){
                //this.$refs.singleSortBar.getDatasList(val.name);
            },

            eerSortClick(){
                if (this.activeTab === 'singleSort') {
                    this.isSEerDesc = !this.isSEerDesc;
                } else {
                    this.isEEerDesc = !this.isEEerDesc;
                }
            },

            setEvaluateStr(evaluateStr){
                this.evaluateStr.tgo = evaluateStr.tgo;
                this.evaluateStr.tg = evaluateStr.tg;
                this.evaluateStr.a = evaluateStr.a;
                this.evaluateStr.n = evaluateStr.n;
                this.evaluateStr.cop = evaluateStr.cop;
                this.evaluateStr.eer = evaluateStr.eer;
            },

            setSumEEREvaluate(sumEER){
                this.evaluateStr.sumEER = sumEER;
            },
            getMark(markEigen){
                this.hasMark = !!markEigen;
            }
        }
    }
</script>
<style>
    #charts {
        position: relative;
    }

    #charts .el-radio-group {
        z-index: 300;
        margin: 0 0 15px;
    }

    .show-result {
        border: none;
        position: absolute;
        left: 200px;
        /*top: 3px;*/
        cursor: pointer;
    }

    .result-card {
        position: absolute;
        left: 270px;
        top: 10px;
        z-index: 1;
        line-height: 22px;
        /*background-color: #e0e0e0;*/
    }

    div#pane-eerSort:after, div#pane-singleSort:after {
        content: ".";
        display: block;
        height: 0px;
        clear: both;
        visibility: hidden;
    }
</style>


