<template>
    <div class="eerparam" :class="{border:allDeltaEERList.length>0}">
        <div :id="activeTab+'Scatter'" v-show="allDeltaEERList.length>1"></div>
        <div class="none-item" style="margin-top: 80px;" v-show="allDeltaEERList.length===1">仅一条数据情况下无△EER数据</div>
    </div>
</template>

<script>
    import echarts from '../../../../lib/echarts.min .js'
    import calculate from '../../../common/calculate.js'

    export default {

        data () {
            return {
                allDeltaEERList: [],
                dataY: [],
                markDeltaEER: 0,
                myChart: {}
            }
        },

        props: ['activeTab', 'isEerDesc', 'isEvaluateItem'],

        beforeDestroy: function () {

            window.removeEventListener("resize", this.resizeHandler);

        },

        watch: {

            '$store.state.evaluate': {
                handler: function (newVal, oldVal) {

                    this.getDatasList();
                },
                deep: true
            },
//            '$route'(to, from){
//                this.getDatasList();
//            },
            isEerDesc: function (newVal, oldVal) {
                this.drawLine();
            },

        },

        methods: {
            getDatasList(){
//                this.allEigenList = JSON.parse(JSON.stringify(this.$store.state.calculate.allEigenList));
                this.allDeltaEERList = JSON.parse(JSON.stringify(this.$store.state.evaluate.allDeltaEERList));
                if (this.allDeltaEERList.length === 0) {
                    console.log("this.allDeltaEERList.length", this.allDeltaEERList.length);
                    return;
                }
                if (this.isEvaluateItem) {
                    var selectedDeltaEERList = this.allDeltaEERList.filter((item) => {
                        return item.id.toString() === this.$route.query.id.toString();
                    });

                    this.markDeltaEER = selectedDeltaEERList.length > 0 ? selectedDeltaEERList[0].value : null;

                    //get sum（EER, ΔEER）Evaluate String
                    var sumEER = selectedDeltaEERList.length > 0 ? selectedDeltaEERList[0].sumEER : null;
                    var sumEERArr = (calculate.getSumEERArr(this.allDeltaEERList)).sort(function (a, b) {
                        return b - a;
                    });
                    //console.log('sumEERArr', sumEERArr);
                    var sumEERMark = sumEERArr.indexOf(sumEER);
                    var sumEEREvaluate = this.getEvaluateStr(sumEERMark, sumEERArr.length);
                    sumEEREvaluate.value = sumEER;
                    this.$emit('setSumEEREvaluate', sumEEREvaluate);
                }
                this.allDeltaEERList.sort(
                    function (a, b) {
                        return a.eer - b.eer;
                    }
                );
                this.drawLine();
            },

            getEvaluateStr(index, leng){
                var oneFour = leng / 4;
                var half = leng / 2;
                var threeFour = half + oneFour;

                if (index < oneFour) {
                    return {
                        isBad: false,
                        str: '前[0,25%)'
                    };
                } else if (index >= oneFour && index < half) {
                    return {
                        isBad: false,
                        str: '[25%-50%)'
                    };
                } else if (index >= half && index < threeFour) {
                    return {
                        isBad: true,
                        str: '[50%-75%)'
                    };
                } else {
                    return {
                        isBad: true,
                        str: '[75%-100%]'
                    };
                }
            },

            drawLine(){
                var self = this;
                this.allDeltaEERList.sort(function (a, b) {
                    return self.isEerDesc ? a.eer - b.eer : b.eer - a.eer;
                });
                this.setY();
                var series = this.getSeries();
                var option = {
                    title: {
                        show: true,
                        text: '● (-)bias           辅助指标△EER',
                        textStyle: {
                            color: 'black',
                            fontSize: '12'
                        },
                        subtext: '● (+)bias',
                        subtextStyle: {
                            color: 'red',
                        }
                    },
                    tooltip: {
                        show: true,
                        trigger: 'axis',
                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                            type: 'cross',
                            axis: 'x'       // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    xAxis: {
                        show: true,
                        type: 'value',
                        position: 'top',
                        splitLine: {show: false},
                        axisTick: {
                            inside: true
                        }
                    },
                    yAxis: {
                        type: 'category',
                        position: 'left',
                        splitLine: {show: true, lineStyle: {type: 'dashed'}},
                        axisLine: {show: true},
                        axisLabel: {show: false},
                        axisTick: {
                            show: false,
                            length: 1,
                        },
                        data: this.dataY,
                    },
                    series: [series]
                };

                let chartContainer = document.getElementById(this.activeTab + 'Scatter');

                this.resizeChart();
                this.myChart = echarts.init(chartContainer);
                this.myChart.setOption(option);
                window.addEventListener("resize", this.resizeHandler);
                this.$nextTick(function () {
                    this.resizeHandler()
                }, 0);
            },
            resizeHandler(){
                this.resizeChart();
                this.myChart.resize();

            },

            resizeChart(){

                let chartContainer = document.getElementById(this.activeTab + 'Scatter');
                let width = (window.innerWidth - 380) * 0.2;
                //let height = window.innerHeight;
                let height = 40;
                chartContainer.style.width = width + 'px';
                //chartContainer.style.height = (window.innerHeight) + 'px';
                // chartContainer.style.height = (height * this.allDeltaEERList.length + 85) + 'px';
                if (this.allDeltaEERList.length === 1) {
                    chartContainer.style.height = "120px";
                } else {
                    chartContainer.style.height = (height * this.allDeltaEERList.length + 85) + 'px';
                }

            },

            setY(){
                var dataY = [];
                for (var i = 0; i < this.allDeltaEERList.length; i++) {
                    dataY.push(i)
                }
                this.dataY = dataY;
            },

            getSeries(){
                var seriesItem = {
                    name: '△EER',
                    type: 'scatter',
                    symbol: 'circle',
                    label: {
                        normal: {
                            show: false
                        },
                        emphasis: {
                            show: true,
                            position: 'top',
                            textStyle: {
                                fontSize: 14,
                                color: 'blue'
                            }
                        }
                    },
                    itemStyle: {
                        itemGap: 20,
                        normal: {
                            show: true,
                            color: function (params) {
                                var index_color = params.value;
                                if (index_color >= 0) {
                                    return 'red';
                                } else {
                                    return 'black';
                                }
                            },
                        }
                    },
                    data: this.allDeltaEERList
                };

                if (this.isEvaluateItem && this.markDeltaEER) {
                    var markDeltaEER = this.markDeltaEER;
                    var markLine = {
                        animation: false,
                        label: {
                            normal: {
                                show: true,
                                formatter: function () {
                                    return markDeltaEER;
                                }
                            }
                        },
                        lineStyle: {
                            normal: {
                                type: 'dotted',
                                color: 'blue',
                            }
                        },
                        data: [[{
                            coord: [markDeltaEER, 0],
                            symbol: 'none'
                        }, {
                            coord: [markDeltaEER, this.allDeltaEERList.length - 1],
                            symbol: 'none'
                        }]]
                    };
                    seriesItem.markLine = markLine;
                } else {
                    seriesItem.markLine = null;
                }
                return seriesItem;
            },

            getSortedDeltaEER(){
                this.allDeltaEERList.sort(function (a, b) {
                    return a.value - b.value;
                });
            },
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .eerparam {
        width: 20%;
        height: 100%;
        margin-right: 80px;
        margin-top: 28px;
        float: right;
    }

    .eerparam p {
        font-size: 12px;
    }

    #singleSortScatter, #eerSortScatter {
        border: 1px solid;
    }

    /* .eerparam.border {
         border: 1px solid;
     }*/
</style>



